// project-list
(function() {

	var $projectLists = $('.project-list');

	if ($projectLists.isset()) {
		$projectLists.each(function() {
			var $projectList = $(this);
			var $menu = $projectList.find('.project-list__nav-menu');
			var $menuItems = $menu.find('[data-filtering-role="menu-item"]');
			var $list = $projectList.find('.project-list__list');
			var $listItems = $list.find('[data-filtering-category]');

			function filterGridItems($items, category) {
				if (category === 'all') {
					$items.show();
				}
				else {
					$items.hide();

					for (var i = $items.length - 1; i >= 0; i--) {
						var currentItem = $items[ i ];
						var categoriesArr = currentItem.getAttribute('data-filtering-category').split(' ');

						for (var j = categoriesArr.length - 1; j >= 0; j--) {
							if (categoriesArr[ j ] === category) {
								currentItem.style.display = '';
							}
						}
					}
				}
			}

			function setActiveMenuItem($items, category) {
				var $links = $items.children('a');

				for (var i = $links.length - 1; i >= 0; i--) {
					if ($links[ i ].getAttribute('href').replace('#/category/', '') === category) {
						var $item = $($links[ i ]).parent();
						$items.removeClass('nav-menu__item_active');
						$item.addClass('nav-menu__item_active');
						return false;
					}
				}
			}

			function checkCategory($menuItems, $listItems, category) {
				filterGridItems($listItems, category);
				setActiveMenuItem($menuItems, category);
			}

			$menuItems.on('click', function() {
				var $item = $(this);
				var $link = $item.children('a');
				var category = $link.attr('href').split('/')[ 2 ];

				checkCategory($menuItems, $listItems, category);
			});

			$(window).on('load', function() {
				var category = window.location.hash.replace('#/category/', '') || 'all';
				checkCategory($menuItems, $listItems, category);
			});

		});
	}

})();
