// mobile-menu
(function() {

	var $mobileMenus = $('.mobile-menu');
	var $page = $('.page');

	if ($mobileMenus.isset()) {
		$mobileMenus.each(function() {
			var $mobileMenu = $(this);
			var $closeBtn = $mobileMenu.find('.mobile-menu__close');

			$closeBtn.on('click', function(event) {
				event.preventDefault();

				if ($page.hasClass('page_menu_open')) {
					$page.removeClass('page_menu_open');
					$.scrollEnable();
				}
			});
		});
	}
})();
