// menu-trigger
(function() {

	var $menuTriggers = $('.menu-trigger');
	var $page = $('.page');

	if ($menuTriggers.isset()) {
		$menuTriggers.each(function() {
			var $trigger = $(this);

			$trigger.on('click', function(event) {
				event.preventDefault();

				$(this).addClass('menu-trigger_active');
				$page.addClass('page_menu_open');
				$.scrollDisable();
			});

		});
	}

})();
